@import url("./product.css");
@import url("./stars.css");

:root {
  --white-color: #fff;
  --border-color: #f3f3f3;
  --border-radius: 5px;
  --main-accent-color: #060b35;
  --secondary-color: #616070;
  --blue-accent-color: #00acdc;
}

* {
  margin: 0;
  box-sizing: border-box;
}

html {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 10px;
}
